import React from "react";
import { Link } from "gatsby";
import "./Post.sass";
import { FaClock, FaComment, FaUser, FaBookmark, FaEdit } from "react-icons/fa"
import * as moment from "moment";
import { getCategory, constructCategoryUrl, getAuthor, INestedCategory } from '../../tool';
import { IAuthor, ICategory, IPostFrontmatter } from "../../interfaces";

interface IProps
{
  author: IAuthor;
  commentCnt: number;
  isPreview?: boolean;
  frontmatter: IPostFrontmatter;
  categories: INestedCategory[];
}

export class PostHeader extends React.Component<IProps> {
  constructor(props) {
    super(props);
  }

  render() {
    const dateFormat = "DD MMM, 'YY"
    let date = moment(this.props.frontmatter.date);
    let update = null;
    if(this.props.frontmatter.updatedate != null && this.props.frontmatter.updatedate != "")
    {
      if(!(Math.abs(date.diff(update)) <= 86400000 || update.isBefore(date) == true))
      {
        update = moment(this.props.frontmatter.updatedate);
      }
    }

    return (
      <div className="post-header">
        <div className="post-meta">
          <div title="Author">
          <FaUser/> 
          <Link className="post-meta-content" to={"/author/"+this.props.author.frontmatter.slug}>{this.props.author != null ? this.props.author.frontmatter.firstname + " " + this.props.author.frontmatter.lastname : ""}
          </Link>
          </div>
          {
            update != null ? <div title="Bearbeitet" className="post-meta-time">
              <FaEdit/>
              <span className="post-meta-content">{update.format(dateFormat)}</span> 
            </div> : ""
          }
          <div title="Angelegt" className="post-meta-time">
            <FaClock/> 
            <span className="post-meta-content">{date.format(dateFormat)}</span> 
          </div>
        </div>
        <div className="post-subtitle">
        {
          this.props.frontmatter.subtitle != null ? <Link to={this.props.frontmatter.slug}>{this.props.frontmatter.subtitle}</Link> : ""
        }
        </div>
        <div>
          <h1>
            <Link to={this.props.frontmatter.slug}>{this.props.frontmatter.title}</Link>
          </h1>
        </div>
      </div>
    );
  }
};

