import { graphql } from "gatsby";
import React from "react";
import { IQueryArrayResult, ICategory, IAuthor, IComment, ISpecialPage, ICustomerStory, IPost, ITag, IGatsbyLocation } from '../../interfaces';
import { ContentLayoutWithSidebar } from '../../components/Layout/ContentLayout';
import { SideBar } from "../../components/SideBar/SideBar";
import { getAuthor, nestCategories, urlJoin } from '../../tool';
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import SideBarCategories from "../../components/SideBar/SideBarCategories";
import { PostPreview } from "../../components/Post/PostPreview";
import { CategoryBar } from "../../components/CategoryBar/CategoryBar";
import { EmptyContent } from "../../components/Content/EmptyContent";
import { PostList } from "../../components/Post/PostList";
import { IHeaderProps } from "../../components/Layout/Header";

interface IQueryResult
{
  data: {
    posts: IQueryArrayResult<IPost>;
    categories: IQueryArrayResult<ICategory>
    authors: IQueryArrayResult<IAuthor>
  }
  context: {
    tag: ITag
  };
  location: IGatsbyLocation;
}

export class Tag extends React.Component<IQueryResult>
{
  constructor(props)
  {
    super(props);
  }
  render()
  {
    let posts = this.props.data.posts.edges
      .map(e => e.node)
      .sort((a,b) => {
        if(a.fields.timestamp > b.fields.timestamp)
          return -1
        else
          return 1;
      });
    let categories = nestCategories(this.props.data.categories.edges.map(e => e.node));
    let authors = this.props.data.authors.edges.map(e => e.node);

    let header: IHeaderProps = {
      url: urlJoin("tag", this.props.context.tag.slug),
      description: `Posts Ã¼ber ${this.props.context.tag.name}`,
      type: "object",
      title: `${this.props.context.tag.name} Archive | Powerpivot Insights | Durchblick durch Daten`,
      tags: [this.props.context.tag.name],
    }

    return (
      <ContentLayoutWithSidebar 
      header={header}
      sidebar={(
        <SideBar isFixed={true}>
          <SideBarQuickstart></SideBarQuickstart>
          <SideBarHelp></SideBarHelp>
          <SideBarCategories></SideBarCategories>
        </SideBar>
      )} 
      >
      <CategoryBar title={"Tags / "+this.props.context.tag.name}></CategoryBar>
      <PostList posts={posts} categories={categories} authors={authors}/>
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({pageContext, data, location}) => {
  return (
    <Tag data={data} location={location} context={pageContext}></Tag>
  )
}
export default Page;


export const pageQuery = graphql`
  query tagsPosts($tagSlug: String!, $now: Int!) {
    posts: allMarkdownRemark(
      filter: {
        fileAbsolutePath: {
          regex: "/.*/data/posts/.*[.]md/"
        }, 
        frontmatter: {
          tags: {eq: $tagSlug}
        }
        fields: {
            publishTimestamp:{
            lt: $now
          }
        }
      }) {
      edges {
        node {
          ...postShort
        }
      }
    }
    ...categories
    ...authorsMarkdown
  }
`
